          <button v-on:click="connectToContract">컨트랙트 연결</button>
        </div>
        <div v-else>
          <!-- 컨트랙트까지 연결이 되었다 -->
          <div class ="functionCard"> <!--functionCard / 아래 4개 div-->
            <!--이름이 들어갈 div-->
